
.home_background_img {
    width: 100%;
    height: 400px;
    object-fit: cover;
}

.home_projects {
    text-align: center;
}

.home_projects ul {
    list-style: none;
    margin-right: 2rem;
}

.home_projects li {
    padding: 0.5rem 0;
    text-decoration: double;
}

.home_projects h1 {
    border-bottom: 0.25rem rgb(77, 77, 77) ridge;
    font-weight: 100;
}

.home_projects_1 h1 {
    font-weight: normal;
    border-bottom: none;
}

.home_projects_1_info {
    margin: 0 0.5rem;
}

.home_projects_1_image {
    object-fit: cover;
    margin: 0 auto;
}

.home_projects_1_image button {
    border-style: none;
    background-color: #333;
    color: white;
    padding: 0.5rem 1rem;
}

.home_projects_1_image button:hover {
    color: #333;
    background-color: lightgreen;
}

.home_projects_2 {
    margin: 5rem;
}

.home_projects_2 h1 {
    font-weight: normal;
    border-bottom: none;
}

.home_projects_2_image {
    object-fit: cover;
    margin: 0 auto;
}

.home_projects_2_image button {
    border-style: none;
    background-color: #333;
    color: white;
    padding: 0.5rem 1rem;
}

.home_projects_2_image button:hover {
    color: #333;
    background-color: lightgreen;
}

@media only screen and (min-width: 1500px) {

    .home_background_img {
        width: 100%;
    }

    .home_projects {
        margin: 0 5rem;
    }

    .home_projects ul {
        list-style: none;
        text-align: center;
        margin-right: 2rem;
    }

    .home_projects li {
        padding: 0.5rem 0;
        text-decoration: double;
    }

    .home_projects_header h1 {
        padding: 0 5rem;
        text-align: left;
    }

    .home_projects h1 {
        border-bottom: 0.25rem rgb(77, 77, 77) ridge;
        font-weight: 100;
        text-align: left;
    }

    .home_projects_1 {
        margin: 0 5rem;
    }

    .home_projects_1 h1 {
        font-weight: normal;
        border-bottom: none;
    }

    .home_projects_1_info {
        float: right;
    }

    .home_projects_1_image {
        float: left;
    }

    .home_projects_1_image img {
        width:  400px;
        height: 400px;
        object-fit: cover;
    }

    .home_projects_1_image button {
        border-style: none;
        background-color: #333;
        color: white;
        padding: 0.5rem 1rem;
    }

    .home_projects_1_image button:hover {
        color: #333;
        background-color: lightgreen;
    }

    .home_projects_2 {
        margin: 45rem 5rem;
    }

    .home_projects_2 h1 {
        font-weight: normal;
        border-bottom: none;
    }

    .home_projects_2_info {
        float: right;
    }

    .home_projects_2_image {
        float: left;
    }

    .home_projects_2_image img {
        width:  400px;
        height: 400px;
        object-fit: cover;
    }

    .home_projects_2_image button {
        border-style: none;
        background-color: #333;
        color: white;
        padding: 0.5rem 1rem;
    }

    .home_projects_2_image button:hover {
        color: #333;
        background-color: lightgreen;
    }
}