
.about_header {
    width: 200px;
    margin: 5rem auto;
    background-color: #333;
    padding: 0.1rem 0rem 0.1rem;
    color: white;
    border-bottom: 0.5rem  rgb(160, 247, 90) solid;
    text-align: center;
}

.about_info_header_education {
    padding: 0 5rem;
}

.about_info_header_education h1 {
    border-bottom: 0.25rem rgb(77, 77, 77) ridge;
    font-weight: 100;
    text-align: center;
}

.about_info_image_education {
    text-align: center;
}

.about_info_image_education img {
    max-width: 100%;
    height: auto;
    text-align: center;
    object-fit: cover;
}

.about_info_text_education {
    text-align: center;
    margin-left: 0.2rem;
    margin-right: 0.2rem;
    border: 0.25rem #333 solid;
}

.about_info_athletics {
    margin-top: 3rem;
}

.about_info_header_athletics {
    padding: 0 5rem;
}

.about_info_header_athletics h1 {
    border-bottom: 0.25rem rgb(77, 77, 77) ridge;
    font-weight: 100;
    text-align: center;
}

.about_info_image_athletics {
    max-width: 100%;
    height: auto;
    text-align: center;
    object-fit: cover;
}

.about_info_text_athletics {
    text-align: center;
    margin-left: 0.2rem;
    margin-right: 0.2rem;
    border: 0.25rem #333 solid;
}

.about_info_personal {
    margin: 3rem 0;
}

.about_info_header_personal {
    padding: 0 5rem;
}

.about_info_header_personal h1 {
    border-bottom: 0.25rem rgb(77, 77, 77) ridge;
    font-weight: 100;
    text-align: center;
}

.about_info_image_personal {
    max-width: 100%;
    height: auto;
    text-align: center;
    object-fit: cover;
}

.about_info_text_personal {
    text-align: center;
    margin-left: 0.2rem;
    margin-right: 0.2rem;
    border: 0.25rem #333 solid;
}

@media only screen and (min-width: 1500px) {

    .about_header {
        width: 200px;
        margin: 5rem auto;
        background-color: #333;
        padding: 0.5rem 1.5rem;
        color: white;
        border-bottom: 0.5rem  rgb(160, 247, 90) solid;
        text-align: center;
    }

    .about_info_header_education {
        padding: 0 5rem;
    }

    .about_info_header_education h1 {
        border-bottom: 0.25rem rgb(77, 77, 77) ridge;
        font-weight: 100;
        text-align: left;
        padding: 0 3rem;
    }

    .about_info_image_education {
        margin-left: 15rem;
        max-width: 100%;
        height: auto;
        float: left;
    }

    .about_info_text_education {
        text-align: center;
        width: 400px;
        float: right;
        margin: 6rem 7rem;
        padding: 0 0.25rem;
        border: 0.5rem #333 solid;
    }

    .about_info_athletics {
        margin-top: 30rem;
    }

    .about_info_header_athletics {
        padding: 0 5rem;
    }

    .about_info_header_athletics h1 {
        border-bottom: 0.25rem rgb(77, 77, 77) ridge;
        font-weight: 100;
        text-align: left;
        padding: 0 3rem;
    }

    .about_info_image_athletics {
        margin-right: 15rem;
        max-width: 100%;
        height: auto;
        float: right;
    }

    .about_info_text_athletics {
        text-align: center;
        width: 400px;
        float: left;
        margin: 6rem 7rem;
        padding: 0 0.25rem;
        border: 0.5rem #333 solid;
    }

    .about_info_personal {
        margin: 35rem 0;
    }

    .about_info_header_personal {
        padding: 0 5rem;
    }

    .about_info_header_personal h1 {
        border-bottom: 0.25rem rgb(77, 77, 77) ridge;
        font-weight: 100;
        text-align: left;
        padding: 0 3rem;
    }

    .about_info_image_personal {
        margin-left: 15rem;
        max-width: 100%;
        height: auto;
        float: left;
    }

    .about_info_text_personal {
        text-align: center;
        width: 400px;
        float: right;
        margin: 6rem 7rem;
        padding: 0 0.25rem;
        border: 0.5rem #333 solid;
    }
}